	module.exports = {"data":{"post":{"html":"<p>To follow up on <a href=\"/2003/05/10/2224.php\">Smoking for Health Care</a>, it's being reported today that the proposed &euro;1 cigarette tax <a href=\"http://www.heute.t-online.de/ZDFheute/artikel/21/0,1367,WIRT-0-2048021,00.html\">will not be imposed all at once</a> on  January 1, but will instead be introduced in stages of 40, 30 and 30 cents. The government otherwise fears a drastic drop in cigarette use and an impact on expected revenue from the new tax.</p>\n<p>That's refreshing honesty, albeit a bit cynical. The government would rather have smokers die than quit. Just make sure you buy a few cartons extra before you go.</p>","fields":{"title":"Please Don't Not Smoke","date":"27 May 2003","url":"https://papascott.de/2003-05-27-please-dont-not-smoke/"}}},"pathContext":{"slug":"/2003-05-27-please-dont-not-smoke/","prev":{"fields":{"title":"hebig and hebig","date":"27 May 2003","slug":"/2003-05-27-hebig-and-hebig/"}},"next":{"fields":{"title":"Blogs Opening Iranian Society?","date":"28 May 2003","slug":"/2003-05-28-blogs-opening-iranian-society/"}}}}

